.field-details {

}

.field-details__list {
  max-width: 100%;
	display: grid;
  grid-template-columns: repeat(4, max-content);
  column-gap: 2.7vw;
  row-gap: 25px;
  justify-content: space-between;
  margin-top: 25px;

  @media (max-width: 1280px) {
    grid-template-columns: repeat(2, max-content);
  }
}

.field-details__item label {
  position: relative;
  font-size: clamp(16px, 1.2vw, 23px);
  padding-left: 50px;

  @media (max-width: 1280px) {
    font-size: 23px;
  }
}

.field-details__item input[type="checkbox"] + label::before {
  position: absolute;
  content: "";
  width: 35px;
  height: 35px;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  background: transparent;
  border: 1px solid var(--back-input);
}

.field-details__item input[type="checkbox"]:checked + label::before {
  position: absolute;
  content: "";
  width: 35px;
  height: 35px;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  border: 1px solid var(--back-input);
  background: url("../img/check.svg");
  background-repeat: no-repeat;
  background-size: 25px 25px;
  background-position: 50% 50%;
}
