div.navigation-header {
  display: grid;
  grid-template-columns: max-content 1fr;
  column-gap: 2vw;
  align-items: center;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 10.4vw;
  padding-right: 10.4vw;

  @media (max-width: 1280px) {
    grid-template-columns: 1fr;
    padding-left: 3.6vw;
    padding-right: 3.6vw;
  }
}


