@mixin _1600 {
    @media (max-width: 1600px){
        @content;
    }
}

@mixin _1280 {
  @media (max-width: 1280px) {
    @content;
  }
}

@mixin _992 {
  @media (max-width: 992px) {
    @content;
  }
}

@mixin _767 {
  @media (max-width: 767px) {
    @content;
  }
}

@mixin _480 {
  @media (max-width: 480px) {
    @content;
  }
}

/*  Viewports  */

$tablet-width: 676px;
$desktop-width: 1280px;
$mobile-width-only: 540px;
$retina-dpi: 144dpi;
$retina-dppx: 1.5dppx;

/*  Colors  */

:root {
  --back-main: #000000;
  --back-gray: #333333;
  --back-dark-gray: #808080;
  --back-link: #ea46e3;
  --basic-white: #ffffff;
  --basic-dark-pink: #ef1993;
  --basic-green: #1dbc08;
  --basic-yellow: #f8c206;
  --basic-orange: #f99205;
  --basic-blue: #4ac9dc;
  --back-input: #23413f;
}
