.site-list {
  display: grid;
  grid-auto-flow: column;
  column-gap: 40px;
  column-gap: 2vw;
  row-gap: 40px;
  margin-left: 5px;

  @media (max-width: 767px) {
    display: none;
  }
}

.site-list a {
  position: relative;
  font-size: clamp(14px, 1.3vw, 20px);
  line-height: 28px;
  color: var(--basic-white);
  text-decoration: none;

  @media (max-width: 1280px) {
    font-size: 18px;
  }

  @media (max-width: 480px) {
    font-size: 25px;
  }

  &:hover {
    color: var(--back-link);
  }
}

.site-list__item--active a {

}
