.catalog {
  padding-top: 38px;
}

.catalog a {
  text-decoration: none;
}

.catalog__present {
  display: grid;
  grid-template-columns: 2fr clamp(157px, 12vw, 235px);
  grid-template-rows: min-content 1fr;
  column-gap: 2vw;
  padding-left: 6vw;
  padding-right: 6vw;

  @media (max-width: 1280px) {
    padding-left: 0;
    padding-right: 0;
  }

  @media (max-width: 767px) {
    grid-template-columns: 1fr;
    text-align: center;
  }
}

.catalog__present > div {
  max-width: 1100px;

  @media (max-width: 767px) {
    max-width: 100%;
  }
}

.catalog__present h2 {


  @media (max-width: 767px) {
    margin-bottom: 25px;
  }
}

.catalog__present p {
  padding-right: 120px;
  margin-top: 32px;

  @media (max-width: 992px) {
    grid-template-columns: 1fr;
    padding: 0;
  }

  @media (max-width: 767px) {
    padding: 0;
  }
}

.catalog__cup {
  grid-column: 2/3;
  grid-row: 1/3;
  margin-left: auto;

  @media (max-width: 767px) {
    grid-column: 1/-1;
    grid-row: 2/3;
    margin: 0 auto;
  }
}

.catalog__list {
  margin-top: 134px;
}
