.form-search {
  position: relative;
  display: flex;
  align-items: center;
  border: none;
}

.form-search input[type="search"] {
  visibility: hidden;
  width: 134px;
  height: 24px;
  padding-left: 10px;
  margin-right: 5px;
  border: 0;
  outline: 0;
  cursor: pointer;

  @media (max-width: 1280px) {
    width: 100%;
  }
}

.form-search button {
  width: 24px;
  height: 24px;
  background: url("../img/search.svg") transparent;
  background-repeat: no-repeat;
  background-size: 100% auto;
  border: none;
  cursor: pointer;
  padding: 0;
}

.form-search input[type="search"]::-webkit-input-placeholder {
  visibility: hidden;
  font-family: inherit;
  color: rgba(0, 0, 0, 0.5);
}

.form-search:focus input[type="search"]::-webkit-input-placeholder,
.form-search:hover input[type="search"]::-webkit-input-placeholder {
  visibility: visible;
}

.form-search:focus input[type="search"],
.form-search:hover input[type="search"] {
  visibility: visible;
}
