.caption {
  font-family: inherit;
  font-size: clamp(42px, 3.2vw, 60px);
  font-variation-settings: "wght" 400, "wdth" 95;
  color: var(--back-gray);
  margin: 0;

  @media (max-width: 1280px) {
    font-size: 42px;
  }

  @media (max-width: 480px) {
    font-size: 34px;
    line-height: 36px;
  }
}
