.field-profiles {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	column-gap: 9.3vw;
  row-gap: 6vw;


  @media (max-width: 992px) {
    grid-template-columns: 1fr;
    text-align: center;
  }
}

.field-profiles__item {
	margin: 0;
}

.field-profiles__item label {
	display: block;
}

.field-profiles__item input[type="text"],
.field-profiles__item input[type="tel"] {
	width: 100%;
	height: 34px;
  padding-left: 10px;
	border: none;
	border-bottom: 2px solid var(--back-input);
}
