.headline {
  font-family: inherit;
  font-size: clamp(50px, 3.9vw, 75px);
  line-height: 4vw;
  font-variation-settings: "wght" 400, "wdth" 95;
  color: var(--back-gray);
  margin: 0;

  @media (max-width: 992px) {
    font-size: clamp(34px, 5vw, 42px);
    line-height: 42px;
  }

  @media (max-width: 480px) {
    line-height: 34px;
  }
}
