.site-link {
  position: relative;
  color: var(--back-main);
  text-decoration: none;
}

.site-link::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 4px;
  left: 0;
  bottom: -4px;
  text-decoration: none;
  z-index: -1;
}

.site-link--pink {
  border-bottom: 4px solid var(--basic-dark-pink);
}

.site-link--orange {
  border-bottom: 4px solid var(--basic-orange);
}

.site-link--green {
  border-bottom: 4px solid var(--basic-green);
}

.site-link--cool-pink {
  border-bottom: 4px solid var(--back-link);
}

.site-link--blue {
  border-bottom: 4px solid var(--basic-blue);
}
