.slider-main {
  position: relative;
  width: 100%;
}

.slider-main__list.swiper-wrapper {
  width: 100%;
  height: 47vw;
  overflow: hidden;

  @media (max-width: 1280px) {
    height: clamp(404px, 60vw, 590px);
  }
}

.slider-main__item {
  display: flex;
  max-width: 100%;
  background-repeat: no-repeat;
  background-size: 100% auto;

  @media (max-width: 1280px) {
    background-size: cover;
  }
}

.slider-main__item--1  {
  background-image: url("../img/slider-main_01.jpg");
}

.slider-main__item--2  {
  background-image: url("../img/slider-main_02.jpg");
}

.slider-main__item--3  {
  background-image: url("../img/slider-main_03.jpg");
}



