.slider-main__toggle {
  position: absolute;
  width: 37px;
  height: 37px;
  bottom: 20px;
  background-color: rgba(255, 255, 255, 0.8);
  border: none;
  cursor: pointer;
  z-index: 10;
}

.slider-main__toggle--prev {
  right: 223px;
  transform: scaleX(-1);
}

.slider-main__toggle--next {
  right: 170px;
}

.slider-main__toggle::before {
  position: absolute;
  content: "";
  width: 13px;
  height: 21px;
  background-image: url("../img/arrows-slider.png");
  background-repeat: no-repeat;
  background-size: 100% auto;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 20;
}



