.circle-block__item {
  position: absolute;
  z-index: 20;

  @media (max-width: 767px) {
    display: none;
  }
}

.circle-block__btn {
  position: relative;
  display: block;
  width: 48px;
  height: 48px;
  background: transparent, url("../img/marker-circle.png");
  background-repeat: no-repeat;
  background-size: 100% auto;
  border: none;
  cursor: pointer;
  z-index: 15;
}

div.circle-block__card {
  position: absolute;
  top: -100px;
  left: 47px;
  z-index: -1;
}

div.circle-block__card--bottom {
  top: 47px;
  left: 0;
}

div.circle-block__card--left {
  left: -346px;
}

.circle-block__card {
  position: relative;
  display: none;
  width: 346px;
  height: 420px;
  background-color: rgba(255, 255, 255, 0.8);
  text-align: center;
}

.circle-block__info.info-card {
  text-align: center;
  padding-top: 30px;
  margin-top: 0;
}

.circle-block__card img {
  padding-top: 23px;
}

.circle-block__item:hover .circle-block__card,
.circle-block__item:focus .circle-block__card,
.circle-block__item:active .circle-block__card {
  display: block;
}
