.promotion {
  position: relative;
}

.promotion > p {
  position: absolute;
  width: 375px;
  top: -5%;
  left: 50%;
  transform: translateX(-50%);
  font-family: inherit;
  font-size: clamp(28px, 2vw, 40px);

  font-variation-settings: "wght" 400, "wdth" 100;
  color: var(--back-main);
  text-align: center;

  @media (max-width: 1280px) {
    font-size: 28px;
  }

  @media (max-width: 480px) {
    width: 100%;
    font-size: 28px;
  }
}
