.form-user.container {
  padding-top: 42px;
  padding-left: 10.4vw;
  padding-right: 10.4vw;
  padding-bottom: 64px;

  @media (max-width: 1280px) {
    padding-left: 3.6vw;
    padding-right: 3.6vw;
  }
}

.form-user__present {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 20px;
  align-items: center;
}

.form-user__present {
  display: grid;
  grid-template-columns: 46.8vw 1fr;
  column-gap: 2vw;
  align-items: center;
  margin-bottom: 85px;

  @media (max-width: 1280px) {
    grid-template-columns: 1fr;
    text-align: center;
  }
}

.form-user__present h3 {
  margin: 0;

  @media (max-width: 1280px) {
    margin-bottom: 23px;
  }
}

.form-user__present p {
  margin: 0;
}

.form-user__filter {
	max-width: 1336px;
  margin-top: 90px;
}

.form-user__filter {
  font-size: clamp(27px, 2.3vw, 34px);
  line-height: 34px;
  font-variation-settings: "wght" 400, "wdth" 100;
  color: var(--back-main);
  margin: 0;

  @media (max-width: 1280px) {

  }
}

.form-user__field {
	padding: 0;
	margin: 0;
	margin-bottom: 58px;
	border: none;
}

.form-user__filter input[type="checkbox"] + label,
.form-user__filter input[type="radio"] + label {
  cursor: pointer;
}

.form-user__filter input,
.form-user__filter textarea {
  font-size: 20px;
  color: var(--back-input);
}

.form-user__note {
  font-size: 25px;
  line-height: 28px;
  margin: 0;
  margin-bottom: 10px;
}

.form-user__note a {
  color: var(--back-main);
}

input.form-user__btn {
  display: block;
  font-size: 34px;
  font-variation-settings: "wght" 400, "wdth" 100;
  color: var(--basic-white);
  margin-left: auto;

  @media (max-width: 992px) {
    margin: 30px auto 0;
  }
}
