.user-slider {
  position: relative;
  width: 100%;
}

.user-slider__list.swiper-wrapper {
  width: 100%;
  height: 46.3vw;
  overflow: hidden;

  @media (max-width: 1280px) {
    height: clamp(345px, 60vw, 600px);
  }
}

.user-slider__item.swiper-slide {
  display: flex;
  max-width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
}

.user-slider__item--1  {
  background-image: url("../img/slider-bottom_01.jpg");
}

.user-slider__item--2  {
  background-image: url("../img/slider-bottom_02.jpg");
}

.user-slider__item--3  {
  background-image: url("../img/slider-bottom_03.jpg");
}




.slider-main__list.swiper-wrapper {
  width: 100%;
  height: 47vw;
  overflow: hidden;

  @media (max-width: 1280px) {
    height: clamp(345px, 60vw, 600px);
  }
}

.slider-main__item {
  display: flex;
  max-width: 100%;
  background-repeat: no-repeat;
  background-size: 100% auto;

  @media (max-width: 1280px) {
    background-size: cover;
  }
}







.slider-main__toggle {
  position: absolute;
  width: 37px;
  height: 37px;
  bottom: 20px;
  background-color: rgba(255, 255, 255, 0.8);
  border: none;
  cursor: pointer;
  z-index: 10;
}

.user-slider__toggle--prev {
  right: 223px;
  transform: scaleX(-1);
}

.user-slider__toggle--next {
  right: 170px;
}


