.users-gallery {
  padding-top: 115px;
}

.users-gallery__present {
  display: grid;
  grid-template-columns: 46.8vw 1fr;
  column-gap: 2vw;
  align-items: center;
  padding-left: 6vw;
  padding-right: 6vw;
  padding-bottom: 47px;

  @media (max-width: 1280px) {
    grid-template-columns: 1fr;
    text-align: center;
  }
}

