.users-gallery__present.container {
  padding-left: 10.4vw;
  padding-right: 10.4vw;

  @media (max-width: 1280px) {
    padding-left: 3.6vw;
    padding-right: 3.6vw;
  }
}

.users-gallery h3 {
  margin: 0;

  @media (max-width: 1280px) {
    margin-bottom: 23px;
  }
}

.users-gallery p {
  margin: 0;
}
