.gallery-card {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 6vw;
  row-gap: 50px;

  @media (max-width: 1280px) {
    grid-template-columns: repeat(3, 1fr);
  }

  @media (max-width: 992px) {
    grid-template-columns: repeat(2, 1fr);
  }

  @media (max-width: 767px) {
    grid-template-columns: 1fr;
  }
}

.gallery-card__item {
  position: relative;
  display: grid;
  text-align: center;

  @media (max-width: 1600px) {

  }
}

.gallery-card img {
  object-fit: cover;
}

.gallery-card__item::before {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: inherit;
  font-variation-settings: "wght" 400, "wdth" 100;
  width: 122px;
  height: 122px;
  color: var(--basic-white);
  text-align: center;
  top: -40px;
  left: 78%;

  @media (max-width: 1600px) {
    width: 100px;
    height: 100px;
  }

  @media (max-width: 1280px) {
    width: 82px;
    height: 82px;
  }
}

.gallery-card__item--new::before {
  content: "новинка";
  font-size: 25px;
  line-height: 25px;
  background-color: var(--basic-green);

  @media (max-width: 1600px) {
    font-size: 21px;
  }

  @media (max-width: 1280px) {
    font-size: 17px;
  }
}

.gallery-card__item--percent::before {
  content: "%";
  font-size: 75px;
  line-height: 55px;
  background-color: var(--basic-yellow);

  @media (max-width: 1600px) {
    font-size: 62px;
  }

  @media (max-width: 1280px) {
    font-size: 50px;
  }
}

.gallery-card__item--hit::before {
  content: "хит продаж";
  padding-left: 10px;
  padding-right: 10px;
  font-size: 25px;
  line-height: 25px;
  background-color: var(--back-link);

  @media (max-width: 1600px) {
    font-size: 21px;
  }

  @media (max-width: 1280px) {
    font-size: 17px;
  }
}

.gallery-card__item::after {
  position: absolute;
  width: 100px;
  right: -33px;
  text-align: center;
  color: var(--basic-white);
  z-index: 5;
}


