.info-card {
  padding-left: 2.5vw;
  padding-right: 2.5vw;
  margin-top: 28px;

  @media (max-width: 992px) {
    padding-left: 6.5vw;
    padding-right: 6.5vw;
  }
}

.info-card p {
  font-family: inherit;
  font-size: 25px;
  line-height: 24px;


  font-variation-settings: "wght" 400, "wdth" 90;
  color: var(--back-main);
  text-align: center;
  margin: 0;
  margin-top: 24px;
}

.info-card span {
  font-size: 42px;

  font-size: clamp(28px, 2vw, 42px);


  font-variation-settings: "wght" 400, "wdth" 90;
  color: #ef1993;

  @media (max-width: 992px) {
    font-size: 34px;
  }

  @media (max-width: 767px) {
    font-size: 42px;
  }
}

span.info-card__old-price {
  font-size: 25px;
  color: var(--back-dark-gray);
  text-decoration: line-through;
}

.info-card__link {
  display: block;
  margin-top: 28px;
}

.info-card__link.site-link {
  font-size: 25px;
  line-height: 28px;
  font-variation-settings: "wght" 400, "wdth" 90;
}

.info-card__link.site-link::after {
  position: absolute;
  content: "";
  width: 45%;
  height: 4px;
  left: 50%;
  transform: translateX(-50%);
  bottom: -4px;
  text-decoration: none;
  z-index: 3;
}
