.user-nav {
  display: grid;
  grid-template-columns: repeat(3, max-content);
  column-gap: 1.8vw;
  justify-content: end;
  align-items: center;

  @media (max-width: 1280px) {
    justify-content: center;
    padding-left: 100px;
  }
}

.user-nav__tel {
  display: grid;
  font-size: clamp(14px, 1.3vw, 25px);
  line-height: 28px;
  color: var(--basic-white);
  text-decoration: none;

  @media (max-width: 1600px) {

  }

  @media (max-width: 1280px) {
    font-size: 20px;
  }

  @media (max-width: 480px) {
    font-size: 25px;
  }
}

.user-nav__cart {
  position: relative;
  display: flex;
  flex-direction: row;

  @media (max-width: 1280px) {

  }
}

.user-nav__cart a {
  text-decoration: none;
}

.user-nav__cart img {
  width: 28px;
  height: 28px;
  object-fit: cover;
}

.user-nav__cart::before {
  position: absolute;
  content: "";
  width: 30px;
  height: 30px;
  top: -8px;
  left: 20px;
  background-image: url("../img/color-circle.svg");
  background-repeat: no-repeat;
}

.user-nav__cart span {
  position: relative;
  font-family: inherit;
  font-size: 18px;
  font-variation-settings: "wght" 400, "wdth" 100;
  color: var(--basic-white);
  top: -15px;
  right: 2px;
}
